import Vue from 'vue'
import Router from 'vue-router'

// 异步加载
// const My = resolve => require(['@/page/my'],resolve)
// const Schedule = resolve => require(['@/page/schedule'],resolve)

const Schedule = r => require.ensure([],() => r(require('@/page/schedule')),'group-schedule')
const My = r => require.ensure([],() => r(require('@/page/my')),'group-my')
const LookSchedule = r => require.ensure([],() => r(require('@/page/lookschedule')),'group-lookSchedule')
const Register = r => require.ensure([],() => r(require('@/page/register'),'group-register'))

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'schedule',
      component: Schedule
    },
    {
		path: '/my',
      	name: 'my',
      	component: My
    },
    {
      path:'/lookSchedule',
      name:'lookSchedule',
      component:LookSchedule
    },
    {
      path:'/register',
      name:'register',
      component:Register
    }
  ]
})



