import Vue from 'vue'
import Vuex from 'vuex'
import my from './my.js'    				// 页面‘我的’状态管理
import schedule from './schedule.js'  	 	// 页面‘赛程’状态管理


Vue.use(Vuex)

const store = new Vuex.Store({
	state:{  	// 全局状态控制
		count:0,
		nowPage:0,
		showCheckcard:false
	},
	mutations:{
		changeNowPage(state,opt){
			state.nowPage = opt.value;
		},
		changeShowCheckcard(state,opt){
			state.showCheckcard = opt.value;
		}
	},
	modules:{  	// 状态模块引入 
		my,
		schedule
	}
})
export default store;


